			//个人信息,
			userInfo: {
				avatarUrl: "",
				balance: 0,
				createIp: '',
				createTime: '',
				credit: '',
				email: '',
				gender: 1,
				id: null,
				lastLoginIp: "",
				lastLoginTime: "",
				lastPaswordEditTime: "",
				nickname: "未登录",
				phone: ''
			},
			user: {
				username: '未登录',
				avatarUrl: '../../static/img/logo.png',
				signature: '点击昵称跳转登录/注册页',
				integral: 0,
				balance: 0,
				envelope: 0
			},
			// 订单类型
			orderList: [{
					key: 0,
					text: '待付款',
					icon: "payment"
				},
				{
					key: 1,
					text: '待发货',
					icon: "move_to_inbox"
				},
				{
					key: 2,
					text: '待收货',
					icon: "local_shipping"
				},
				{
					key: 3,
					text: '待评价',
					icon: "local_florist"
				},
				{
					key: 5,
					text: '退换货',
					icon: "swap_vertical_circle"
				}
			],
			// 工具栏列表
			mytoolbarList: [{
					url: '../user/collection/collection',
					text: '我的收藏',
					icon: 'favorite'
				},
				{
					url: '../user/coupon/coupon',
					text: '优惠券',
					icon: 'local_play'
				},

				{
					url: '../user/address/address',
					text: '收货地址',
					icon: 'location_on'
				},
				{
					url: '',
					text: '账户安全',
					icon: "settings"
				},
				{
					url: '',
