					icon: 'assignment_turned_in'
				},
				{
					url: '',
					text: '客服',
					icon: 'contact_phone'
				}
			],
			hasCheckedIn: false
		}
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onLoad() {
		this.statusHeight = 0;



		this.loadMyUserInfo()
	},
	onReady() {
		//此处，演示,每次页面初次渲染都把登录状态重置
		uni.setStorage({
			key: 'UserInfo',
			data: false,
			success: function() {},
			fail: function(e) {}
		});
	},
	onShow() {

	},
	methods: {
		//消息列表
		toMsg() {
			uni.navigateTo({
				url: '../msg/msg'
			})
		},
		toOrderList(index) {
			uni.navigateTo({
				url: '../user/order-list/order-list?tbIndex=' + index
			})
		},
		toSetting() {
			uni.navigateTo({
				url: '../user/setting/setting'
			})
		},
		toMyQR() {
			uni.navigateTo({
				url: '../user/my-qr-code/my-qr-code'
			})
		},
		toLogin() {
			uni.showToast({
				title: '请登录',
				icon: "none"
			});
			uni.navigateTo({
				url: '../login/sign-in'
			})
			this.isfirst = false;
		},
		isLogin() {
			//实际应用中,用户登录状态应该用token等方法去维持登录状态.
			const value = uni.getStorageSync('UserInfo');
			if (value) {
				return true;
			}
			return false
		},
		toDeposit() {
			uni.navigateTo({
				url: '../user/deposit/deposit'
			})
		},
		toPage(url) {
			console.log("url: " + url);
			uni.navigateTo({
				url: url
			})
		},
		toCheckIn() {
			uni.navigateTo({
				url: '../user/activity/check-in'
			})
		},
		loadMyUserInfo() {
			let that = this
			this.$pin.request('GET', '/commons/user/info', null,
				successData => {
					that.userInfo = successData.data.user
					that.hasCheckedIn = successData.data.hasCheckedIn
				},
				failData => {
					console.log(failData)
					uni.showToast({
						icon: 'none',
						title: '加载用户信息时出错'
					})
				}
			)
		}
	}
}
